import React from 'react'
import Affair from './Affair'
import {AffairType, FilterType} from './HW2'
import style from './Affairs.module.css'

type AffairsPropsType = {
    data: Array<AffairType>
    filter: FilterType
    setFilter: (value: FilterType) => void
    deleteAffairCallback: (_id: number) => void
}

function Affairs(props: AffairsPropsType) {
    const mappedAffairs = props.data.map((a: AffairType) => (
        <Affair // should work
            key={a._id} // кеи ОБЯЗАТЕЛЬНЫ в 99% - так что лучше их писать всегда при создании компонент в мапе
            affair={a}
            deleteAffairCallback={props.deleteAffairCallback}
        />
    ))

    // const setAll = () => {props.setFilter('all')} // need to fix
    // const setHigh = () => {props.setFilter('high')}
    // const setMiddle = () => {props.setFilter('middle')}
    // const setLow = () => {props.setFilter('low')}
    const set = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        props.setFilter(e.currentTarget.value as FilterType)
    }
    const setStyle = (filter: FilterType) => {
        return props.filter === filter ? style.buttonActive : style.button
    }

    return (
        <div>

            {mappedAffairs}

            <button onClick={set} className={setStyle('all')} value={'all'}>All</button>
            <button onClick={set} className={setStyle('high')} value={'high'}>High</button>
            <button onClick={set} className={setStyle('middle')} value={'middle'}>Middle</button>
            <button onClick={set} className={setStyle('low')} value={'low'}>Low</button>
        </div>
    )
}

export default Affairs
